<template>
  <div>
    <!--    最上层-->
    <v-tabs
      v-model="currentItem"
      slider-color="grey darken-2"
      right
    >
      <v-tab
        v-for="item in items"
        :key="item.id"
        :href="'#' + item.id"
        @click="getMenu(item.id)"
        class="font-weight-black"
        :style="{backgroundColor: (item.abnormal === 1?'#FFEBEE':'')}"
      >
        #{{ item.id }}
      </v-tab>
    </v-tabs>
    <v-tabs-items v-model="currentItem">
      <!--      详情-->
      <v-tab-item
        v-for="item in items"
        :key="item.id"
        :value="item.id"
      >
        <v-card flat height="90">
          <v-row>
            <v-col cols="10" class="py-2">
              <v-card-text class="py-1">
                #{{ item.id }}-{{item.text}}-{{unit}}-{{currentProcess}}
                <!--筛选器-->
              </v-card-text>
            </v-col>
            <v-col cols="2">
              <v-menu offset-y>
                <template
                  v-slot:activator="{ on, attrs }"
                >
                  <v-btn icon small
                         v-bind="attrs"
                         v-on="on">
                    <v-icon>mdi-filter</v-icon>
                  </v-btn>
                </template>
                <v-list>
                  <v-list-item
                    v-for="item in filters"
                    :key="item.key"
                    @click="changeFilter(item.key)"
                    :disabled="item.key===currentFilter"
                  >
                    <v-list-item-title>
                      {{ item.title }}
                    </v-list-item-title>
                  </v-list-item>
                </v-list>
              </v-menu>
            </v-col>
          </v-row>
        </v-card>
      </v-tab-item>
      <v-row>
        <!--        左侧-->
        <v-col cols="3" class="px-0">
          <v-virtual-scroll
            :items="details"
            :item-height="50"
            :height="height"
          >
            <template
              v-slot="{item}"
            >
              <v-card
                dense
                color="grey lighten-3"
                elevation=3
              >
                <v-card-text
                  @click="getDetail(item.id,item.process)"
                  :style="{backgroundColor:(item.id===currentMenu?'#FFFFFF':'')}"
                  class="align-center pr-2"
                >
                  <v-row>
                    <v-col cols="3" class="py-0 pl-1">
                      <!--                      已完成-->
                      <v-icon small v-if="item.status === 0" :color="item.unusual === 1?'#FF5252':''">
                        mdi-checkbox-marked
                      </v-icon>
                      <!--                      正在-->
                      <v-icon small v-else-if="item.status === 2" :color="item.unusual === 1?'#FF5252':''">
                        mdi-checkbox-blank-outline
                      </v-icon>
                      <!--                      未开始-->
                      <v-icon small v-else-if="item.status === 1" :color="item.unusual === 1?'#FF5252':''">
                        mdi-checkbox-blank-circle
                      </v-icon>
                      <!--                      别人-->
                      <v-icon small v-else-if="item.status === 3" :color="item.unusual === 1?'#FF5252':''">
                        mdi-account-multiple
                      </v-icon>
                    </v-col>
                    <v-col cols="9" class="py-0 px-1">
                      <p :class="item.id===currentMenu?'font-weight-black':''" class="caption align-center">
                        {{ item.process | ellipsis}}
                      </p>
                    </v-col>
                  </v-row>
                </v-card-text>
              </v-card>
            </template>
          </v-virtual-scroll>
        </v-col>
        <!--        右侧-->
        <v-col cols="9" class="pl-0">
          <v-virtual-scroll
            :items="['1']"
            :item-height="1600"
            :height="height"
          >
            <template v-shot="{item}">
              <!--              手风琴组件-->
              <v-expansion-panels popout v-model="panel">
                <v-expansion-panel
                  v-for="text in texts"
                  :key="text.id"
                  :readonly="text.status===1"
                >
                  <v-expansion-panel-header>
                    <template v-slot:default="{ open }">
                      <v-row no-gutters @click="getVerification(text.id)">
                        <v-col><h3>{{text.text}}</h3></v-col>
                      </v-row>
                    </template>
                  </v-expansion-panel-header>
                  <v-expansion-panel-content eager>
                    <v-form ref="form">
                      <v-text-field :label="label1"></v-text-field>
                      <v-file-input accept="image/*"
                                    v-model="pic"
                                    @change="Photograph()"
                                    :label=labelList[0]
                                    capture
                                    prepend-icon="mdi-camera"
                                    dense
                                    small-chips
                      >
                      </v-file-input>
                      <v-btn class="float-right" @click="onUploadImg">提交</v-btn>
                      <v-btn text class="float-right mx-4">取消</v-btn>
                    </v-form>
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panels>
            </template>
          </v-virtual-scroll>
        </v-col>
      </v-row>
    </v-tabs-items>
  </div>
</template>

<script>
  // import {getKettle, getProcessAndAction} from "../utils/api";

  export default {
    data: () => ({
      currentItem: null,//当前釜次
      currentMenu: null,//当前工序
      items: [],//釜次列表
      unit: '',//当前所属单元操作
      currentProcess: '',//当前工序
      details: {},//当前釜次下的工序
      height: null,//动态获取设备高度
      texts: {},//当前釜次-工序的操作动作
      label1: '输入温度（°）',//测试用
      filters: [{title: '待办任务', key: 1}, {title: '全部任务', key: 2}, {title: '釜次全部任务', key: 3}],//筛选器列表
      currentFilter: 1,//当前筛选状态
      labelList: ['拍照'],//label列表
      finSrc:[]
    }),

    methods: {
      // getKettleList: function () {
      //   let that = this;
      //   getKettle().then(data => {
      //     if (data.status === 200) {
      //       let res = that.transXml(data);
      //       that.items = res;
      //       let number = res[0];
      //       that.getProcessAndActionList(number)
      //     }
      //   })
      // },
      // getProcessAndActionList: function (number) {
      //   let that = this;
      //   getProcessAndAction(number).then(data => {
      //     if (data.status === 200) {
      //       let res2 = that.transXml(data);
      //       that.datas = res2;
      //       that.currentVal = res2[0].status
      //     }
      //   })
      // },
      // 测试接口-获取釜次列表
      // getlist: function () {
      //   this.$axios.request({
      //     url: 'http://192.168.3.12:88/list',
      //     method: 'get'
      //   }).then(data => {
      //     this.items = data.data['data'];
      //     // 重新赋值当前釜次 触发Watch方法 发送getMenu方法
      //     this.currentItem = this.items[0].id
      //   })
      // },
      // 测试接口-获取工序列表
      getMenu: function (index) {
        this.$axios.request({
          url: 'http://192.168.3.12:88/menu/' + index,
          method: 'get'
        }).then(data => {
          this.details = data.data['data'];
          this.getNewDetail(this.details[0].id, this.details[0].process);
          this.currentMenu = this.details[0].id;
        })
      },
      // 测试接口-获取工序对用操作动作（动作文字）
      getDetail: function (id, process) {
        this.$axios.request({
          url: 'http://192.168.3.12:88/action/' + id,
          method: 'get'
        }).then(data => {
          this.unit = data.data['unit'];
          this.texts = data.data['data'];
          this.currentMenu = id;
          this.currentProcess = process;
          this.getNewMenu(this.currentItem)
        })
      },
      getNewMenu: function (index) {
        this.$axios.request({
          url: 'http://192.168.3.12:88/menu/' + index,
          method: 'get'
        }).then(data => {
          this.details = data.data['data']
        })
      },
      getVerification: function (id) {
        this.$axios.request({
          url: 'http://192.168.3.12:88/verification/' + id,
          method: 'get'
        }).then(data => {
          data.data
        })
      },
      getFirst: function () {
        this.$axios.request({
          url: 'http://192.168.3.12:88/first/',
          method: 'get'
        }).then(data => {
          this.items = data.data['items'];
          this.details = data.data['details'];
          this.unit = data.data['unit'];
          this.currentMenu = this.details[0].id;
          this.texts = data.data['data'];
          this.currentProcess = this.details[0].process
        })
      },

      getNewDetail: function (id, process) {
        this.$axios.request({
          url: 'http://192.168.3.12:88/action/' + id,
          method: 'get'
        }).then(data => {
          this.unit = data.data['unit'];
          this.currentMenu = id;
          this.currentProcess = process;
        })
      },
      // 筛选器：默认选中我的进行中，并保持目前筛选的状态在选择列表Disabled
      changeFilter: function (key) {
        this.currentFilter = key
      },
      Photograph() {
        let that = this;
        let file = this.pic;
        console.log(file.size / 1024000 + 'MB')
        let reader = new FileReader();
        reader.readAsDataURL(file);

        reader.onload = function () {
          let replaceSrc = reader.result;
          console.log(replaceSrc);
          let data = that.compressedImage({
            src: replaceSrc,
            quality: 0.5,
            success: function (src) {
              console.log(src);
              let todoList = [];
              todoList[0] = src;
              that.finSrc = todoList
            }
          });
        }
      },
      compressedImage: function (params) {
        let that = this;

        let initParams = {
          src: params.src || "",
          quality: params.quality || 1,
        };

        let image = new Image();
        image.src = initParams.src;
        image.onload = function () {
          //获取图片初始宽高
          let width = image.width;
          let height = image.height;
          //判断图片宽度，再按比例设置宽度和高度的值
          if (width > 1024) {
            width = 1024;
            height = Math.ceil(1024 * (image.height / image.width));
          }

          //将图片重新画入canvas中
          let canvas = document.getElementById("compressCanvas");
          if (!canvas) { //创建一个canvas画布
            let body = document.body;
            canvas = document.createElement("canvas"); //创建canvas标签
            canvas.id = "compressCanvas"; //给外层容器添加一个id
            canvas.style.position = "fixed";
            canvas.style.zIndex = "-1";
            canvas.style.opacity = "0";
            canvas.style.top = "-100%";
            canvas.style.left = "-100%";
            body.append(canvas);
          }

          let context = canvas.getContext("2d");
          canvas.width = width;
          canvas.height = height;
          context.beginPath();
          context.fillStyle = "#ffffff";
          context.fillRect(0, 0, width, height);
          context.fill();
          context.closePath();
          context.drawImage(image, 0, 0, width, height);
          let replaceSrc = canvas.toDataURL("image/jpeg", initParams.quality); //canvas转DataURL(base64格式)

          params.success && params.success(replaceSrc);
        };
      },
      dataURLtoFile: function (dataUrl, fileName) {
        let arr = dataUrl.split(','), mime = arr[0].match(/:(.*?);/)[1],
          bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
        while (n--) {
          u8arr[n] = bstr.charCodeAt(n);
        }
        return new File([u8arr], fileName, {type: mime});
      },
      onUploadImg(){
        let that = this;
        let imgTempList = that.finSrc;
        imgTempList.map(function (imgItem, imgIndex) {
          let files = that.dataURLtoFile(imgItem, 'pj' + Date.now() + '.jpg'); //DataURL转File
          console.log('Size'+ files.size/ 1024000)
        })

      }
    },
    created() {
      this.getFirst();
      // 获取屏幕高度动态计算左侧滑块高度
      this.height = window.innerHeight - 250;
    },
    // 监听Tab页左右滑动 !=null 解决初次加载就监听问题; 监听筛选器切换，切换后重新获取釜次对应工序
    watch: {
      currentItem(newName, oldName) {
        if (oldName !== null) {
          this.getMenu(this.currentItem);
        }
      },
      currentFilter() {
        this.getMenu(this.currentItem)
      }
    },
    // 截取前4位文字
    filters: {
      ellipsis(value) {
        if (!value) return '';
        if (value.length > 4) {
          return value.substring(0, 4) + '..'
        }
        return value
      }
    }
  }
</script>

<style>
  .v-tab {
    min-width: 60px !important;
  }
</style>



