<template>
  <v-app>
    <!--头部-->
    <v-app-bar
      app
      color="teal lighten-1"
      dark
      @click="logIn ? drawer = !drawer : clickFn()"
    >
      <div class="d-flex align-center">
        <v-img
          alt="Vuetify Logo"
          class="shrink mr-2"
          contain
          src="./static/001.png"
          transition="scale-transition"
          width="40"
        />
      </div>

      <v-toolbar-title>现场作业管理系统</v-toolbar-title>

      <v-spacer></v-spacer>
      <span class="mr-2" v-if="logIn">{{ name }}</span>
      <v-btn
        icon
        large
        v-if="logIn"
      >
        <v-avatar
          size="42px"
          item
          color="grey lighten-3"
          @click.stop="drawer = !drawer"
        >
          <v-img
            src="../static/worker.png"
            alt="Vuetify"
          />
        </v-avatar>
      </v-btn>
      <v-btn
        icon
        large
        v-else
      >
        <v-avatar
          size="42px"
          item
          @click.stop="clickFn()"
        >
          <v-icon large>mdi-account-circle</v-icon>
        </v-avatar>
      </v-btn>
    </v-app-bar>
    <!--中间区域-->
    <v-main>
      <v-container fluid>
        <router-view></router-view>
      </v-container>
    </v-main>
    <!--底部-->
    <v-bottom-navigation
      fixed
      grow
    >
      <v-btn :to="homeLink" style="height: 56px">
        <v-icon medium>mdi-home</v-icon>
      </v-btn>

      <v-btn :to="appsLink" style="height: 56px">
        <v-icon large>mdi-briefcase-outline</v-icon>
      </v-btn>

      <v-btn @click="goBack" style="height: 56px">
        <v-icon medium>mdi-arrow-left</v-icon>
      </v-btn>
    </v-bottom-navigation>
    <!--左侧抽屉-->
    <v-navigation-drawer
      dark
      v-model="drawer"
      fixed
      app
      touchless
      disable-resize-watcher
      width="180"
      src="./static/bg-2.jpg"
    >
      <v-list>
        <v-list-item>
          <v-list-item-avatar>
            <v-img src="../static/worker.png"></v-img>
          </v-list-item-avatar>
        </v-list-item>

        <v-list-item link>
          <v-list-item-content>
            <v-list-item-title class="title">{{ title }}: {{ name }}</v-list-item-title>
            <v-list-item-subtitle>{{ factory }} {{ department }} {{ workshop }}</v-list-item-subtitle>
          </v-list-item-content>

        </v-list-item>
      </v-list>
      <v-divider></v-divider>
      <v-list
        nav
        dense
      >
        <v-list-item-group v-model="item" color="primary">
          <v-list-item
            v-for="(item, i) in items"
            :key="i"
          >
            <v-list-item-icon>
              <v-icon v-text="item.icon"></v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title v-text="item.title"></v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
      <template v-slot:append>
        <div class="pa-6">
          <v-btn color="grey darken-1" block @click="logOut">退出登录</v-btn>
        </div>
      </template>
    </v-navigation-drawer>
  </v-app>
</template>

<script>
  import localforage from 'localforage'

  export default {
    name: 'App',

    data: () => ({
      homeLink: '/',
      appsLink: '/task',
      drawer: false,
      name: '张三',
      title: '操作员',
      factory: '渭南',
      department: '合成二部',
      workshop: '6#西',
      items: [
        {title: 'Dashboard', icon: 'mdi-view-dashboard'},
        {title: 'Photos', icon: 'mdi-image'},
        {title: 'About', icon: 'mdi-help-box'},
      ],
    }),
    mounted() {

    },
    methods: {
      goBack: function () {
        this.$router.go(-1)
      },
      clickFn: function f() {
        this.$router.push({name: 'Login.vue'})
      },
      logOut: function () {
        this.$store.commit('logout_method');
        this.drawer = false
      }
    },
    computed: {
      logIn: function () {
        return this.$store.state.logIn
      }
    },
    created() {
      localforage.getItem('token').then(value => {
        if (value) {
          this.$store.state.logIn = true;
          this.$store.state.token = value
        }
      }).catch(error => {
        console.log(error)
      });
      localforage.getItem('role').then(value => {
        if (value) {
          this.$store.state.role = value
        }
      }).catch(error => {
        console.log(error)
      });
    }
  }
</script>



