<template>
  <v-container fluid class="px-2">
    <v-carousel hide-delimiters height="300">
      <v-carousel-item
        v-for="(item,i) in items"
        :key="i"
        :src="item.src"
      ></v-carousel-item>
    </v-carousel>
    <v-row dense class="pt-8">
      <v-col
        v-for="card in cards"
        :key="card.title"
        :cols="card.flex"
      >
        <v-card color="teal lighten-1">
          <v-card-text>
            <h3>{{ card.title }}</h3>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn icon>
              <v-icon>mdi-bookmark</v-icon>
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  export default {
    data() {
      return {
        items: [
          {
            src: 'http://www.xarlm.com/repository/image/E03V6USKRqaFbB-uUIFNEQ.jpg_560xaf.jpg',
          },
          {
            src: 'http://www.xarlm.com/repository/image/qwu2754NS46QBxWJkdKOPw.jpg_560xaf.jpg',
          },
          {
            src: 'http://www.xarlm.com/repository/image/ZFP86BWnSOeUDoYjgBQRUA.png_560xaf.png',
          },
          {
            src: 'http://www.xarlm.com/img/051.jpg',
          },
          {
            src: 'http://www.xarlm.com/img/052.jpg',
          },
          {
            src: 'http://www.xarlm.com/img/054.jpg',
          },
          {
            src: 'http://www.xarlm.com/img/111.png',
          },
        ],
        cards: [
          {title: '任务分发', flex: 4},
          {title: '任务移交', flex: 4},
          {title: '个人中心', flex: 4},
        ],
      }
    },
  }
</script>



