<template>
  <v-snackbar
    v-model="snackbar"
    bottom
    :timeout="timeout"
  >
    {{message}}
  </v-snackbar>
</template>

<script>
	export default {
		name: 'message',

		data() {
			return {
				message: '',
                snackbar:false,
                timeout: 2000,
			}
		}
	}
</script>




